import { DLRecruitingProvider } from './../../../providers/dl-recruiting-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { pageCommon } from '../../page.base';
/**
 * Generated class for the DlRecruitmentListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dl-recruitment-list',
  templateUrl: 'dl-recruitment-list.html',
})
export class DlRecruitmentListPage extends pageCommon{
  postList: any[];
  title: string = ''
  constructor(public navCtrl: NavController,
    public navParams: NavParams ,
    public recruitingProvider: DLRecruitingProvider){

      super(navCtrl)
      this.title = this.navParams.data.title

      this.recruitingProvider.GetJobList ( ).subscribe((data)=>{
        if(data.errcode == 0){
          this.postList = data.result;
        }
      }); 

 

      
   }
  openDetail(item){
    this.navCtrl.push('DlRecruitmentPage',item)
  }
  
  openQuickRegistration(){
    this.navCtrl.push('QuickRegistrationPage')
    
  }

  doRefresh(_event){
    this.recruitingProvider.GetJobList ( ).subscribe((data)=>{
      super.completeRefresh(_event);
      if(data.errcode == 0){
        this.postList = data.result;
      }
    },err=>{
      super.completeRefresh(_event);
    });   
  }
}



