 
import { Events, NavController } from 'ionic-angular'; 
 
/**
 * page 公共类
 */
export class pageCommon   {
 
  constructor(public navCtrl: NavController, 
    public events?: Events, ) {
 
  }
 
  /**
   * 水平scroll寬度
   * @param width scroll 寬度
   * @param height scroll 高度
   */
  scrollWidth(width?:number,height?:number) {
    return {
      "width": width || (window.screen.availWidth - 5)   + 'px',
      "height": (height || 44) + 'px',
    }
  }

  /**
   * 發佈下拉完成事件
   * @param _event 下拉事件
   */
  completeRefresh(_event){
    _event && _event.complete();
  }

  /**
   * 發佈上拉完成事件
   * @param _event 上拉事件
   */
  completedoInfinite(_event){
    _event && _event.complete();
  }
 
    
}

export interface IProudList{
  doRefresh(_event: any):void; 
  doInfinite(_event: any):void;
}
 


